.search-input{
    background-color: yellowgreen !important
}
.image-list {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    grid-gap: 0 1px;
    grid-auto-rows: 10px;
    grid-auto-columns: 10px;
}
.image-list img {
    width: 300px;
    grid-row-end: span 2;
}

.image-search {
    background-color:  #669966 !important
}
